import React from 'react'
import {BrowserRouter as Router, Route, Switch} from 'react-router-dom';

//Navbar
import Home from './newPro';
import About from './newPro2';
import Todo from './useReducer/useReducer';
import Error from './error';
import Person from './person/'

const App = ()=>{

    return(
        <Router>
            <Switch>
                <Route exact path="/">
                    <Home />
                </Route>
                <Route path="/about">
                    <About />
                </Route>
                <Route path="/Todolist">
                    <Todo />
                </Route>
                <Route path="/person/:id" children={<Person/>}>

                </Route>
                <Route path="*">
                    <Error />
                </Route>
            </Switch>           
        </Router>
    )
}


export default App;