import React, {useReducer, useState} from 'react'
import Modal from "./modal"
import Handle from "./handle"
import {Link} from "react-router-dom";

const data = [];


const App = ()=>{
    
    const defaultState = {
        people:data,
        isModal:false,
        message:"",
        text:false,
    }
    
    const [state, dispatcher] = useReducer(Handle,defaultState)

    const [person, setPerson] = useState({fullName:"",email:""});

    useState(()=>{
        if(state.people === ""){
            console.log(state.people)
        }
    })

    const change = (e)=>{
        const name = e.target.name
        const value = e.target.value

        setPerson({...person,[name]:value})
    }

    const remove = (e, id) =>{       
        dispatcher({type:"REMOVE", payload: {e , id}})
    }

    const trigger = ()=>{
        dispatcher({type:"CLOSE_MODAL"})
    }
    const submit = (e) =>{
        e.preventDefault()
        if(person.fullName === ""){
            dispatcher({type:"NO_NAME"});
            return
        }
        if(person.email === ""){
            dispatcher({type:"NO_EMAIL"})
            return
        }

        const newItem = {id: new Date().getTime().toString(),...person
        }

        dispatcher({type:"ADD_ITEM", payload:newItem})
        setPerson({fullName:"",email:""})
    }

    return (
        <div className="container">
            <div className="navbar">
                <ul className="ul-list">
                    <li><Link to="/">HOME</Link></li>
                    <li><Link to="/about">ABOUT</Link></li>
                    <li><Link to="/Todolist">TODO LIST</Link></li>
                </ul>
            </div>
             <div className="grid2">
                {state.isModal ? <Modal message={state.message} removeModal={trigger}/>: ""}   
                <form className="form-container form" onSubmit={(e)=>submit(e)}>
                <div className="card">
                    <label htmlFor="fullName">Full Name:</label>
                    <input 
                        type="text" 
                        id="fullName" 
                        name="fullName"
                        value = {person.fullName}
                        onChange = {(e)=>change(e)}
                    />
                    <label htmlFor="email">Email:</label>
                    <input 
                        type="email" 
                        id="email" 
                        name="email"
                        value = {person.email}
                        onChange = {(e)=>change(e)}
                    />
                    <button type="submit" className="btn focus">Add Item</button>
                </div>
                
            </form>
            {state.text ? <div className="names">
            <div className="sticky"><h5>TODO LIST</h5></div>
            {state.people.map(person => {
                const {fullName, email, id} = person
                
                return (
                    <div className="wrapper" key={id}>
                        <h6>{fullName}</h6>
                        <p>{email}</p>
                        <button className="remove btn " onClick={(e)=>remove(e,id)}>Remove</button>
                    </div>
                    
                        )
                    })}
                </div> 
                : ""
            }
             </div>
        </div>
    )
}

export default App;