import React, {useState} from "react";
import img1 from "../images/1.jpg"
import img2 from "../images/2.jpg"
import img3 from "../images/3.jpg"
import {Link} from 'react-router-dom'

const data = [
    {
        id:1,
        name:"james",
        image:img1
    
    },
    {
        id:2,
        name:"john",
        image:img2
    
    },
    {
        id:3,
        name:"Samuel",
        image:img3
    
    }
]

// const getUSer = (person) => {
    
// }
const App = () => {
    const [users, setUsers] = useState(data)

    const remove = (id) => {
        const newPeople = users.filter(person => person.id !== id);
        setUsers(newPeople)
    }
    const recover = () => {
        setUsers(data)
    }

    const change = (e) =>{
        e.target.classList.toggle("click")
        console.log(e)
    }
    return (
        <div className="container">
            <div className="navbar">
                <ul className="ul-list">
                    <li><Link to="/">HOME</Link></li>
                    <li><Link to="/about">ABOUT</Link></li>
                    <li><Link to="/Todolist">TODO LIST</Link></li>
                </ul>
            </div>
            <div className="grid2">
                {users.map((person)=>{
                    const {id , name, image} = person
                    return(
                        <div className="card" key={id}>
                            <img src={image} alt={name} onClick={(e)=> change(e)}/>
                            <h4>{name}</h4>
                            <button className="btn focus" onClick={() => remove(id)}>Remove</button>
                        </div>
                    )
                })}
                <div>
                    <button className="btn focus" onClick={() => setUsers([])}>Clear All</button>
                    <button className="btn focus" onClick={recover}>Recover All</button>
                </div>
            </div>
        </div>
    )
}

export default App;