import React , { useEffect, useState } from 'react';
import {Link} from 'react-router-dom';
 
const App = () => {

    const [empty, setEmpty] = useState("")
    const [person, setPerson] = useState({fullName:"", email:""});
    const [data, setData] = useState([])
    const [text, setText] = useState(false)


    const check = (t) =>{
        if(data === ""){
            setEmpty("All Item Cleared");
            return 
        }
        setEmpty(t)
    }

    useEffect(()=>{
        if(empty !== ""){
            setTimeout(()=>{
                setEmpty("")
            },3000)
        }

        if(data.length === 0){
            setText(false)
        }
    },[data, empty])
    const submit = (e) =>{
        e.preventDefault()
        if(person.fullName === ""){
            check("Fullname Required")
            return
        }
        if(person.email === ""){
            check("Enter an Email Address")
            return
        }
        setData([...data, {...person , id: new Date().getTime().toString()}])
        setText(true)
        setPerson({fullName:"", email:""})
        check("1 Item Added");
    }

    const change = (e) =>{
        const name = e.target.name
        const value = e.target.value
        setPerson({...person, [name]:value })
    }

    const remove = (e) => {
        const newData = data.filter(d => d.id !== e)
        setData(newData);
        setTimeout(()=>{
            check("1 Item Removed");
        },200)
    }
    return (
        <div className="container">
        <div className="navbar">
                <ul className="ul-list">
                    <li><Link to="/">HOME</Link></li>
                    <li><Link to="/about">ABOUT</Link></li>
                    <li><Link to="/Todolist">TODO LIST</Link></li>
                </ul>
        </div>
        <div className="grid2">
            {empty ? <h5 style={{color:"red", background:"whitesmoke",padding:"5px"}}>{empty}</h5> : ""}
            <form className="form-container form" onSubmit={(e)=>submit(e)}>
                <div className="card">
                    <label htmlFor="fullName">Full Name:</label>
                    <input 
                        type="text" 
                        id="fullName" 
                        name="fullName"
                        value = {person.fullName}
                        onChange = {(e)=>change(e)}
                    />
                    <label htmlFor="email">Email:</label>
                    <input 
                        type="email" 
                        id="email" 
                        name="email"
                        value = {person.email}
                        onChange = {(e)=>change(e)}
                    />
                    <button type="submit" className="btn focus">Add Item</button>
                </div>
                
            </form>
            {text ? <div className="names">
            <h5>TODO LIST</h5>
            {data.map(person => {
                const {fullName, email, id} = person
                
                return (
                    <div className="wrapper" key={id}>
                        <h6>{fullName}</h6>
                        <p>{email}</p>
                        <button className="remove btn " onClick={()=>remove(id)}>Remove</button>
                    </div>
                    
                        )
                    })}
                </div> 
                : ""
            }
        </div>
        
    </div>
    )
}


export default App;