import React, {useEffect} from 'react'

const Modal = ({message, removeModal}) =>{

    useEffect(()=>{
        setTimeout(()=>{
            removeModal()
        },2000)
    })

    return(
        <p style={{color:"red", background:"whitesmoke",padding:"5px", fontWeight:600}}>{message}</p> 
    )
}

export default Modal;