const handle = (state, action) => {
    if(action.type === "NO_NAME"){
        return {...state,isModal:true,message:"FULLNAME REQUIRED"};
    }

    if(action.type === "NO_EMAIL"){
        return {...state,isModal:true,message:"NO EMAIL INSERTED"};
    }

    if(action.type === "ADD_ITEM"){
        const payload = [...state.people, action.payload]
        return {...state,text:true, people:payload,isModal:true,message:"1 ITEM ADDED"};

    }
    if(action.type === "REMOVE"){
        const newPeople = state.people.filter((person => {
            return person.id !== action.payload.id
    
        }))
        if(newPeople.length === 0){
            return {...state,people:newPeople,isModal:true,message:"All ITEM REMOVED",text:false};
        }
        return {...state,people:newPeople,isModal:true,message:"1 ITEM REMOVED",text:true};

        
    }

    if(action.type === "CLOSE_MODAL"){
        return{...state, isModal:false}
    }
}

export default handle;